.cmn-toggle-switch {
  display: block;
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 50px;
  height: 50px;
  font-size: 0;
  text-indent: -9999px;
  -webkit-appearance: none;
  -moz-appearance: none;
  background: transparent;
  appearance: none;
  box-shadow: none;
  border-radius: none;
  border: none;
  cursor: pointer;
  -webkit-transition: background 0.3s;
  transition: background 0.3s; }

.cmn-toggle-switch span {
  display: block;
  position: absolute;
  top: 24px;
  left: 9px;
  right: 9px;
  height: 2px;
  background: black; }

.cmn-toggle-switch span::before,
.cmn-toggle-switch span::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: black;
  content: ""; }

.cmn-toggle-switch span::before {
  top: -8px; }

.cmn-toggle-switch span::after {
  bottom: -8px; }

.main-menu__btn--closed.cmn-toggle-switch__htx {
  background-color: transparent; }

.cmn-toggle-switch__htx span {
  -webkit-transition: background 0 0.3s;
  transition: background 0 0.3s; }

.cmn-toggle-switch__htx span::before,
.cmn-toggle-switch__htx span::after {
  -webkit-transition-duration: 0.3s, 0.3s;
  transition-duration: 0.3s, 0.3s;
  -webkit-transition-delay: 0.3s, 0;
  transition-delay: 0.3s, 0; }

.cmn-toggle-switch__htx span::before {
  -webkit-transition-property: top, -webkit-transform;
  transition-property: top, transform; }

.cmn-toggle-switch__htx span::after {
  -webkit-transition-property: bottom, -webkit-transform;
  transition-property: bottom, transform; }

/* active state, i.e. menu open */
.cmn-toggle-switch__htx.active span {
  background: none; }

.cmn-toggle-switch__htx.active span::before {
  top: 0;
  background: black;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg); }

.cmn-toggle-switch__htx.active span::after {
  bottom: 0;
  background: black;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg); }

.cmn-toggle-switch__htx.active span::before,
.cmn-toggle-switch__htx.active span::after {
  -webkit-transition-delay: 0, 0.3s;
  transition-delay: 0, 0.3s; }

.about {
  padding: 20px 15px; }

.about__desc {
  text-align: center; }

.about__item-title {
  font-size: 18px;
  font-weight: 700;
  text-align: center; }

.about__item-desc {
  font-size: 14px;
  text-align: center; }

.about__list {
  margin: 50px 0; }

.about__item::before {
  content: '';
  display: block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 66px;
  height: 66px;
  margin: 0 auto; }

.about__item:first-child::before {
  background-image: url(../img/about00.png); }

.about__item:nth-child(2)::before {
  background-image: url(../img/about01.png); }

.about__item:nth-child(3)::before {
  background-image: url(../img/about02.png); }

.about__item:nth-child(4)::before {
  background-image: url(../img/about03.png); }

.about__item:nth-child(5)::before {
  background-image: url(../img/about04.png); }

.about__item:last-child::before {
  background-image: url(../img/about05.png); }

@media (min-width: 768px) {
  .about__list {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between; }
  .about__item {
    width: 45%;
    margin-bottom: 20px; }
  .about__item-title {
    display: block;
    min-height: 44px; } }

@media (min-width: 1170px) {
  .about {
    padding-left: calc(50% - 585px);
    padding-right: calc(50% - 585px); }
    .about .main__btn {
      margin-top: 0; }
  .about__item-title {
    min-height: auto; }
  .about__item {
    width: 370px;
    padding: 0 15px;
    margin-bottom: 60px; }
    .about__item:last-child .about__item-title {
      min-height: 44px; }
  .about__list {
    margin-bottom: 0; }
  .about__desc {
    width: 570px;
    margin: 20px auto; } }

