body {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  line-height: normal;
  margin: 0;
  padding: 0;
  color: black;
  font-size: 16px; }

button {
  cursor: pointer; }

a {
  text-decoration: none;
  color: black; }

* {
  box-sizing: border-box; }

.main__title {
  font-weight: 500;
  font-size: 30px;
  color: #003D82;
  text-align: center;
  margin: 20px 0; }

.main__btn {
  background: linear-gradient(90deg, #194EA5 0%, #134699 100%);
